import './App.css';
import Header from './Components/Header.js';
import Home from './Components/Home.js';
import Restriction from './Components/Restriction.js';
import FAQ from './Components/FAQ.js';
import Facemask from './Components/facemask/Facemask'
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom';
import Footer from './Components/Footer.js';
import Donate from './Components/donation/Donate';
import Blogs from './Components/Blogs.js';
import Products from './Components/Products.js';
import Login from './Components/donation/Login';
import Register from './Components/donation/Register'

function App() {
  return (
    <div className="App">
      
      
      <Router>
      <Header />
      <Switch>    
             <Route path='/' component={Home} exact/>
             <Route path='/Restriction' component={Restriction} exact/>               
             <Route path='/FAQ' component={FAQ}/> 
             <Route path='/Facemask' component={Facemask} exact/>
             <Route path='/Donate/*' component={Donate} exact/>
             <Route path='/Blogs' component={Blogs} exact/>
             <Route path='/Products' component={Products} exact/>
             <Route  path='/Login' component={Login} exact />
            <Route  path='/Register' component={Register} exact />
      </Switch>
      
      <Footer />
      </Router>
    </div>
  );
}

export default App;
