import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "./Auth";
import firebaseConfig from "../../config.js";
import { Form, Button, Card, Alert } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom"
import login from '../Animations/login.json';
import LottieAnimation from '../Lottie';


const Login = () => {
  const handleSubmit = (e) => {
    e.preventDefault();
    const { email, password } = e.target.elements;
    try {
      firebaseConfig.auth().signInWithEmailAndPassword(email.value, password.value);
    } catch (error) {
      alert(error);
    }
  };
  const { currentUser } = useContext(AuthContext);
  if (currentUser) {
    return <useNavigate to="/Donate/Dashboard" />;
  }
  return (
    <div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-8" style={{marginLeft:"135px"}}>
        
          <div class="account-content">
            <div class="row align-items-center justify-content-center">
              <div class="col-md-7 col-lg-6">
              <LottieAnimation lotti = {login} height={700} width={700} />
              </div>
              <div class="col-md-12 col-lg-6">

                <Card style={{marginLeft: "110px", padding : "40px 10px 60px 10px"}}>
                  <h1 className="text-center mb-4">Log In</h1>
                  <Form onSubmit={handleSubmit}>
                  <Form.Group id="email">
                    <Form.Label for="email">Email</Form.Label>
                    <Form.Control type="email" name="email" placeholder="Email" />
                  </Form.Group>
                  <Form.Group id="password">
                    <Form.Label for="password">Password</Form.Label>
                    <Form.Control type="password" name="password" placeholder="Password" />
                    <Button  className="w-100" type="submit">Submit</Button>
                  </Form.Group>
                  </Form>
                </Card>
                <div className="w-100 text-center mt-2">
                  Need an account? <Link to="/Donate/Register"> Register</Link>
                </div>

              </div>
              </div>
              </div>
              
              </div>
      </div>
    </div>
  </div>
  );
};

export default Login;
