import React from 'react'
import LottieAnimation from '../Lottie';
import git from '../Animations/git.json';
function Facemask() {
    return (
        <div className="facemask" id="facemask">
            <div className="container" style={{paddingTop:"100px"}}>
                <div className="row">
                    <div className="col-lg-6">
                    <LottieAnimation lotti = {git} height={500} width={500} />
                    </div>
                    <div className="col-lg-6">
                        <h1 style={{color:"blue", paddingBottom:"60px"}}>Facemask Detection</h1>
                        <h4>View my another project on real-time facemask detection</h4>
                        <button className="btn btn-dark" ><a href="https://github.com/Rishupal-13/face-mask-detection" style={{color:"white"}}>Click here</a></button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Facemask
