import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "./Auth";
import firebaseConfig from "../../config.js";
import atb from "../images/atbjpg.jpg";
import { Link } from 'react-router-dom';

const Dashboard = () => {
  const { currentUser } = useContext(AuthContext);
  if (!currentUser) {
    return <useNavigate to="/Donate/Login" />;
  }
  return (
    <div className="dashboard" id="dashboard" style={{marginTop:"80px"}}>
    <img src={atb} className="img-fluid" />
      <h1 style={{color: "green"}}>Welcome</h1>
      <p>This is the dashboard, if you can see this you're logged in.</p>

      <Link to="/Donate"  className="btn btn-danger" role="button" aria-pressed="true" onClick={() => firebaseConfig.auth().signOut()}> 
                  SignOut
        
            </Link>
    </div>
  );
};

export default Dashboard;