import React, { useContext } from "react";
import { AuthContext } from "./Auth";
import { Link } from 'react-router-dom';
import {Card} from 'react-bootstrap';
import left from '../images/don1.png';

const Home = () => {
  const { currentUser } = useContext(AuthContext);
  
  return (
  
    <div>
      <div class="container-fluid" style={{marginTop: "200px", marginBottom:"200px"}}>
        <div class="row">
          <div class="col-md-8 offset-md-2">
          
            <div class="account-content">
              <div class="row align-items-center justify-content-center">
                <div class="col-md-7 col-lg-6 login-left">
                <img src={left} className="img-fluid" />
                </div>
                <div class="col-md-12 col-lg-6 login-right">
                <Card style={{boxShadow: "0 10px 20px rgba(0,0,0,0.12), 0 6px 6px rgba(0,0,0,0.18)",  border: "1px solid #ccc",
        borderRadius: "20px"}}>
          <h1>Home</h1>
        
          {currentUser ? (
            <p>
              You are logged - <Link to="/Donate/Dashboard">View Dashboard</Link>
            </p>
          ) : (
            <p>
            <ul>
            <li>
            <Link to="/Donate/Login"  className="btn btn-success" role="button" aria-pressed="true">
                  Login
        
            </Link>
                </li>OR
                <li>
                <Link to="/Donate/Register"  className="btn btn-success" role="button" aria-pressed="true" onclick={()=>console.log("hello")}>
                  Register
        
            </Link>
                
            
                </li>
               
                </ul>
            </p> 
          )}
          </Card>
          <div className="w-100 text-center mt-2">
               Or View Login status?<Link to="/Donate/Dashboard"> Dashboard</Link>
                </div>
          

          </div>
          </div>
          </div>
          
          </div>
        </div>
      </div>
    </div>
    
  );
};

export default Home;