
import React, {useState} from "react";
import { useNavigate } from "react-router-dom";
import firebaseConfig from "../../config";
import { Form, Button, Card} from "react-bootstrap"
import register2 from '../Animations/register2.json';
import LottieAnimation from '../Lottie';
import { Link, useHistory } from "react-router-dom"

const Register = () => {
  const [currentUser, setCurrentUser] = useState(null);    
  const handleSubmit = (e) => {
    e.preventDefault();    
    const { email, password } = e.target.elements;
    try {
      firebaseConfig.auth().createUserWithEmailAndPassword(email.value, password.value);      
      setCurrentUser(true);
    } catch (error) {
      alert(error);
    }
  };
  if (currentUser) {
      return <useNavigate to="/Donate/Dashboard" />;
  }
  return (
    <div>
    <div class="container-fluid" style={{marginTop:"160px", marginLeft: "20px"}}>
      <div class="row">
        <div class="col-md-8" style={{marginLeft:"145px", paddingLeft:"30px"}}>
        
          <div class="account-content">
            <div class="row align-items-center justify-content-center">
              <div class="col-md-7 col-lg-6">
              <LottieAnimation lotti = {register2} height={300} width={300} />
              </div>
              <div class="col-md-12 col-lg-6">

                <Card style={{marginLeft: "0px", padding : "40px 10px 60px 10px"}}>
                  <h1>Register</h1>
                  <Form onSubmit={handleSubmit}>
                  <Form.Group id = "email">
                    <Form.Label for="email">Email</Form.Label>
                    <Form.Control type="email" name="email" placeholder="Email" />
                  </Form.Group>
                  <Form.Group>
                    <Form.Label for="password">Password</Form.Label>
                    <Form.Control type="password" name="password" placeholder="Password" />
                  </Form.Group>

                    <Button className="w-100" type="submit">Submit</Button>
                  </Form>
                  </Card>
                <div className="w-100 text-center mt-2">
                  Already have an account? <Link to="/Donate/Login" > Login</Link>
                </div>

              </div>
              </div>
              </div>
              
              </div>
      </div>
    </div>
  </div>
  );
};

export default Register;